.button {
  position: relative;
  z-index: 1;
  display: inline-flex;
  align-items: center;
  justify-content: center;

  padding: size(12px) size(20px);
  font-family: $ff-main;
  font-weight: 500;
  font-size: size(16px);
  line-height: size(20px);
  color: $color-default-white;

  white-space: nowrap;
  border: none;
  background-color: $color-transparent;

  transition: $trans-default;
  cursor: pointer;

  &__bg {
    position: absolute;
    z-index: -1;
    height: size(44px);

    @include vp-767 {
      width: auto;
      height: size-mob(44px);
    }
  }

  @include vp-767 {
    padding: size-mob(6px) size-mob(16px);
    font-size: size-mob(16px);
    line-height: size-mob(20px);

    .is-hidden {
      display: none;
    }
  }

  &--dark {
    &::before {
      background-color: $color-default-black;
    }

    .button__decor {
      color: $color-default-black;
    }
  }

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  &__text {
    position: relative;
    z-index: 3;
  }

  @media (hover: hover),
  screen and (min-width: 0\0) {

    &:hover,
    &:focus-visible {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.5;
    }
  }
}
