.intro {
  position: relative;
  height: 100vh;
  max-height: size(800px);

  &__video-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    video {
      width: 100%;
    }
  }

  &__content {
    position: relative;
    z-index: 1;
    max-width: size(610px);
    padding-top: 19%;
  }

  &__qr-code {
    margin-top: size(40px);
    color: $color-default-white;
  }

  &__bubbles {
    position: absolute;
    top: size(175px);
    left: size(1031px);

    width: size(400px);
  }

  &__bubble-message {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;

    height: size(104px);

    animation-name: bubbleAnim;
    animation-duration: 6s;
    animation-timing-function: ease;
    animation-iteration-count: infinite;

    &:nth-of-type(1) {
      animation-delay: 0s;
    }

    &:nth-of-type(2) {
      animation-delay: 2s;
    }

    &:nth-of-type(3) {
      animation-delay: 4s;
    }
  }

  &__bubble-message--1 {
    width: size(335px);
  }

  &__bubble-message--2 {
    width: size(210px);
  }

  &__bubble-message--3 {
    width: size(286px);
  }
}
